@import "../tools";
@table-prefix-cls: ~"@{css-prefix}grid";
@table-select-item-prefix-cls: ~"@{table-prefix-cls}-filter-select-item";

.@{table-prefix-cls} {
  &-wrapper {
    position: relative;
    border: 1px solid @border-color-base;
    border-bottom: 0;
    border-right: 0;
  }
  width: inherit;
  height: 100%;
  max-width: 100%;
  overflow: hidden;
  color: @text-color;
  font-size: @font-size-small;
  background-color: #fff;
  box-sizing: border-box;
  //position: relative;
  &-hide {
    opacity: 0;
  }

  &:before {
    content: '';
    width: 100%;
    height: 1px;
    position: absolute;
    left: 0;
    bottom: 0;
    background-color: @border-color-base;
    z-index: 1;
  }

  &:after {
    content: '';
    width: 1px;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    background-color: @border-color-base;
    z-index: 3;
  }

  &-with-header {
    //border-radius: @border-radius-base @border-radius-base 0 0;
  }

  &-with-footer {
    //border: 1px solid @border-color-base;
    //border-radius: 0 0 @border-radius-base @border-radius-base;
  }

  &-with-header&-with-footer {
    //border-radius: @border-radius-base;
  }

  &-footer,
  &-title {
    height: 48px;
    line-height: 48px;
    border-bottom: 1px solid @border-color-split;
  }

  &-footer {
    border-bottom: none;
  }

  &-header {
    overflow: hidden;
  }

  &-body {
    //overflow: auto;
    //position: relative;
  }

  &-overflowX {
    overflow-x: scroll;
  }

  &-overflowY {
    overflow-y: scroll;
  }

  &-tip {
    overflow-x: auto;
    overflow-y: hidden;
    //position: relative;
  }

  &-with-fixed-top&-with-footer {
    .@{table-prefix-cls} -footer {
      border-top: 1px solid @border-color-base;
    }

    tbody tr:last-child td {
      border-bottom: none;
    }
  }

  td,
  th {
    min-width: 0;
    height: 48px;
    box-sizing: border-box;
    text-align: left;
    text-overflow: ellipsis;
    vertical-align: middle;
    //position: relative;
    border-bottom: 1px solid @border-color-split;
  }

  th {
    height: 40px;
    white-space: nowrap;
    overflow: hidden;
    background-color: @table-thead-bg;
  }

  td {
    background-color: #fff;
    transition: background-color @transition-time @ease-in-out;
  }

  td &-column,
  th &-column {
    &-left {
      text-align: left;
    }

    &-center {
      text-align: center;
    }

    &-right {
      text-align: right;
    }
  }

  & table {
    //width: 100%;
    table-layout: fixed;
  }

  &-border {
    td,
    th {
      border-right: 1px solid @border-color-split;
    }
  }

  &-cell {
    padding-left: 18px;
    padding-right: 18px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal;
    word-break: break-all;
    box-sizing: border-box;

    &-ellipsis {
      word-break: keep-all;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    &-with-expand {
      height: 47px;
      line-height: 47px;
      padding: 0;
      text-align: center;
    }

    &-expand {
      cursor: pointer;
      transition: transform @transition-time @ease-in-out;

      i {
        font-size: @font-size-base;
      }

      &-expanded {
        transform: rotate(90deg);
      }
    }

    &-sort {
      cursor: pointer;
      user-select: none;
    }
  }

  &-hidden {
    visibility: hidden;
  }

  th &-cell {
    display: inline-block;
    //position: relative;
    word-wrap: normal;
    vertical-align: middle;
  }

  td &-expanded-cell {
    padding: 20px 50px;
    background: @table-thead-bg;
  }

  &-stripe &-body,
  &-stripe &-fixed-body {
    tr:nth-child(2n) {
      td {
        background-color: @table-td-stripe-bg;
      }
    }
    // #1380
    tr.@{table-prefix-cls} -row-hover {
      td {
        background-color: @table-td-hover-bg;
      }
    }
  }

  tr &-row-hover {
    td {
      background-color: @table-td-hover-bg;
    }
  }

  &-large {
    font-size: @font-size-base;

    th {
      height: 48px;
    }

    td {
      height: 60px;
    }

    &-footer,
    &-title {
      height: 60px;
      line-height: 60px;
    }

    .@{table-prefix-cls} -cell-with-expand {
      height: 59px;
      line-height: 59px;

      i {
        font-size: @font-size-base+2;
      }
    }
  }

  &-small {
    th {
      height: 32px;
    }

    td {
      height: 40px;
    }

    &-footer,
    &-title {
      height: 40px;
      line-height: 40px;
    }

    .@{table-prefix-cls} -cell-with-expand {
      height: 39px;
      line-height: 39px;
    }
  }

  &-row-highlight,
  &-stripe &-body tr&-row-highlight:nth-child(2n),
  &-stripe &-fixed-body tr&-row-highlight:nth-child(2n),
  tr&-row-highlight&-row-hover {
    td {
      background-color: @table-td-highlight-bg;
    }
  }

  &-fixed,
  &-fixed-right {
    position: absolute;
    top: 0;
    left: 0;
    box-shadow: 2px 0 6px -2px rgba(0, 0, 0, 0.2);

    &::before {
      content: '';
      width: 100%;
      height: 1px;
      background-color: @border-color-base;
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 4;
    }
  }

  &-fixed-right {
    top: 0;
    left: auto;
    right: 0;
    box-shadow: -2px 0 6px -2px rgba(0, 0, 0, 0.2);
  }

  &-fixed-right-header {
    position: absolute;
    top: -1px;
    right: 0;
    background-color: @table-thead-bg;
    border-top: 1px solid @border-color-base;
    border-bottom: 1px solid @border-color-split;
  }

  &-fixed-header {
    overflow: hidden;

    &-with-empty {
      .@{table-prefix-cls} -hidden {
        .@{table-prefix-cls} -sort {
          display: none;
        }

        .@{table-prefix-cls} -cell span {
          display: none;
        }
      }
    }
  }

  &-fixed-body {
    overflow: hidden;
    position: relative;
    z-index: 3;
  }

  &-fixed-shadow {
    width: 1px;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    box-shadow: @shadow-right;
    overflow: hidden;
    z-index: 1;
  }

  &-sort {
    .sortable();
  }

  &-filter {
    display: inline-block;
    cursor: pointer;
    position: relative;

    i {
      color: @btn-disable-color;
      transition: color @transition-time @ease-in-out;

      &:hover {
        color: inherit;
      }

      &.on {
        color: @primary-color;
      }
    }

    &-list {
      padding: 8px 0 0;

      &-item {
        padding: 0 12px 8px;

        .ivu-checkbox-wrapper + .ivu-checkbox-wrapper {
          margin: 0;
        }

        label {
          display: block;

          & > span {
            margin-right: 4px;
          }
        }
      }

      ul {
        padding-bottom: 8px;
      }
      .select-item(@table-prefix-cls, @table-select-item-prefix-cls);
    }

    &-footer {
      padding: 4px;
      border-top: 1px solid @border-color-split;
      overflow: hidden;

      button:first-child {
        float: left;
      }

      button:last-child {
        float: right;
      }
    }
  }

  &-tip {
    table {
      width: 100%;

      td {
        text-align: center;
      }
    }
  }

  &-expanded-hidden {
    visibility: hidden;
  }
}

.ivu-table-popper {
  min-width: 0;
  text-align: left;

  .ivu-poptip-body {
    padding: 0;
  }
}
